<template>
  <view class="custom-tab-bar">
    <view 
      v-for="(item, index) in tabList" 
      :key="index"
      class="tab-item"
      :class="{ active: currentPath === item.pagePath }"
      @tap="switchTab(item.pagePath)"
    >
      <text class="tab-icon" :class="{ 'icon-active': currentPath === item.pagePath }">{{ item.icon }}</text>
      <text class="tab-text" :class="{ 'text-active': currentPath === item.pagePath }">{{ item.text }}</text>
    </view>
  </view>
</template>

<script>
export default {
  name: 'CustomTabBar',
  data() {
    return {
      currentPath: '/pages/index/index',
      tabList: [
        {
          pagePath: '/pages/index/index',
          text: '首页',
          icon: '🏠'
        },
        {
          pagePath: '/pages/school/index',
          text: '学校',
          icon: '🏫'
        },
        {
          pagePath: '/pages/policy/index',
          text: '政策',
          icon: '📋'
        },
        {
          pagePath: '/pages/ai/index',
          text: '智能咨询',
          icon: '💬'
        }
      ]
    }
  },
  methods: {
    switchTab(path) {
      if (this.currentPath === path) return
      this.currentPath = path
      uni.switchTab({
        url: path
      })
    }
  },
  mounted() {
    const pages = getCurrentPages()
    if (pages.length > 0) {
      const currentPage = pages[pages.length - 1]
      this.currentPath = '/' + currentPage.route
    }
  }
}
</script>

<style lang="scss">
.custom-tab-bar {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  height: 100rpx;
  background-color: #ffffff;
  display: flex;
  padding-bottom: env(safe-area-inset-bottom);
  box-shadow: 0 -2rpx 12rpx rgba(0, 0, 0, 0.04);
  
  .tab-item {
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 10rpx 0;
  }
  
  .tab-icon {
    font-size: 40rpx;
    color: #7A7E83;
    margin-bottom: 4rpx;
    
    &.icon-active {
      color: #4aa3ff;
    }
  }
  
  .tab-text {
    font-size: 24rpx;
    color: #7A7E83;
    
    &.text-active {
      color: #4aa3ff;
    }
  }
}
</style> 